/**
 * BASE
 *******************************************/
// Default colour for objectsâ€™ borders etc.
$base-ui-color: #333 !default;

// Base font-family
$base-font-family: sans-serif!default;

// Using 1.618 Golden Ratio - find it here http://type-scale.com/
// Base stuff - in px --> to be converted to rems
$base-font-size:	17 !default;

$base-line-height: $base-font-size * 1.25 !default;

// Headings
$alpha-size:		100	!default; // h1, .h_alpha
$beta-size:			64	!default; // h2, .h_beta
$gamma-size:		40	!default; // h3, .h_gamma
$delta-size:		24	!default; // h4, .h_delta
$epsilon-size:		18	!default; // h5, .h_epsilon
$zeta-size:			15	!default; // h6, .h_zeta
$eta-size:			14	!default; // h6, .h_zeta for mobile resolutions

$p-size:            $base-font-size !default;


/**
 * FONTS
 *******************************************/

$primaryFont: "Open Sans", "Trebuchet MS", Helvetica, Arial, sans-serif;
$secondaryFont: "Hind", "Trebuchet MS", Helvetica, Arial, sans-serif;
$tertiaryFont: 'Libre Baskerville', Georgia, 'Times New Roman', serif;

$fonticon: 'customicon';

$base-spacing-unit: $base-line-height!default;
$half-spacing-unit: $base-spacing-unit / 2!default;
$line-height-ratio: $base-line-height / $base-font-size;

// Weights
$reg: 400;
$bold: 700;


/**
 * COLOUR VARIABLES
 *******************************************/

$black:             #000;
$white:             #fff;
$grey:              #909090;
$l-grey:            #e0e0e0;
$d-grey:            #454545;
$ivory:             #F2F2F2;
$carbon:            #252525;
$sand:              #CCB899;

$accent-1:          #f95a48; // orange
$accent-2:          #14FF64; // turquoise


/*
 * @TO DO: See if you can use this array to reference the required colours
 * in various modules, as opposed to just looping through it in _helpers
 */

$colours: (
    black:          #000,
    white:          #fff,
    grey:           #909090,
    l-grey:         #e0e0e0,
    d-grey:         #454545,
    ivory:          #F2F2F2,
    carbon:         #252525,
    sand:           #CCB899,
    accent-1:       #f95a48, // orange
    accent-2:       #14FF64 // turquoise
);

/**
 * BASE MEDIA QUERY BREAKPOINTS
 *******************************************/

$mq-mob: 480px!default;
$mq-mob--sml: 320px!default;
$mq-mob--lrg: 640px!default;


$mq-tab: 768px!default;
$mq-tab--sml: 765px!default;
$mq-tab--lrg: 820px!default;


$mq-desk: 1024px!default;
$mq-desk--sml: 960px!default;
$mq-desk--lrg: 1280px!default;