.overlay {

    position: fixed;
    z-index: -1;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;

    background: $white;

    opacity: 0;

    transition: z-index 0.35s linear,
                opacity 0.35s linear;

    &.is-active {

        z-index: 150;

        opacity: 0.9;

    }

}